.scrollTop {
  position: fixed;
  width: 100px;
  bottom: 20px;
  right: 0px;
  align-items: center;
  height: 20px;
  justify-content: center;
  z-index: 1000;
  cursor: pointer;
  animation: fadeInArrow 0.3s;
  transition: opacity 0.4s;
  opacity: 0.5;
  color: grey;
}

.scrollTop:hover {
  opacity: 1;
}

@keyframes fadeInArrow {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}

